<template>
  <div class="footer">
    <div class="buyFooter">
      <cube-tab-bar v-model="selectedLabelDefault" @click="clickHandler">
        <cube-tab v-for="item in tabs" :label="item.label" :key="item.label">
          <!-- name为icon的插槽 -->
          <i slot="icon" :class="item.icon"></i>
          <!-- 默认插槽 -->
          <div class="footerLable">{{ item.label }}</div>
        </cube-tab>
      </cube-tab-bar>
    </div>
  </div>
</template>

<script>
export default {
  name: "tabBarFooter",
  data() {
    return {
      selectedLabelDefault: "首页",
      tabs: [
        {
          label: "首页",
          icon: "icon iconfont buy-icon-shouye"
        },
        {
          label: "分类",
          icon: "icon iconfont buy-icon-shangpinfenlei"
        },
        {
          label: "购物车",
          icon: "icon iconfont buy-icon-gouwuche"
        },
        {
          label: "个人",
          icon: "icon iconfont buy-icon-wode"
        }
      ]
    };
  },
  methods: {
    clickHandler(label) {
      // if you clicked home tab, then print 'Home'
      if (label === "首页") {
        this.$router.push("/");
      } else if (label === "分类") {
        this.$router.push("classify");
      } else if (label === "购物车") {
        this.$router.push("cart");
      } else if (label === "个人") {
        this.$router.push("user");
      }
      this.selectedLabelDefault = label;
    },
    changeHandler() {
      // if you clicked different tab, this methods can be emitted
    }
  },
  mounted() {
    console.log("dasdasda", this.$route);
    if (this.$router.history.current.path === "/") {
      this.selectedLabelDefault = "首页";
    }
    if (this.$router.history.current.path === "/classify") {
      this.selectedLabelDefault = "分类";
    }
    if (this.$router.history.current.path === "/cart") {
      this.selectedLabelDefault = "购物车";
    }
    if (this.$router.history.current.path === "/user") {
      this.selectedLabelDefault = "个人";
    }
  },
  components: {}
};
</script>

<style scoped></style>
