<template>
  <div class="homeGoods">
    <div class="moduleTitle clearfix">
      <span class="leftTitle fl">{{ goodsList[0].title }}</span>
    </div>
    <div class="homegoodsCont">
      <div class="homeList" v-for="(item1, index1) of goodsList" :key="index1">
        <div class="">
          <img
            v-lazy="item1.head_imgs ? config.cdnbase + item1.head_imgs : ''"
            ref="homelist"
            alt=""
            :style="conheight"
          />
        </div>
        <div>
          <div>
            <span class="homeSitename">{{ item1.sitename }}</span>
            <span class="homePrice">{{ item1.money }}{{ item1.price }}</span>
          </div>
          <div>
            <p class="status gradientPrice">特价</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "homeGoods",
  data() {
    return {
      conheight: {
        height: ""
      }
    };
  },
  props: {
    goodsList: {
      type: Array
    }
  },
  mounted() {
    this.getHeight();
  },
  computed: {
    ...mapState(["config"])
  },
  methods: {
    getHeight() {
      this.conheight.height = this.$refs.homelist[0].offsetWidth + "px";
      // console.log('---------------------------------------');
      console.log(this.$refs.homelist[0].offsetWidth);
    }
  },
  created() {}
};
</script>

<style scoped></style>
