        />
        <!--搜索-->
        <router-link to="/search" class="buySearch" alt="search" slot="search">
          <span class="buyGlassIcon icon iconfont  buy-icon-sousuo"></span>
          <span>搜索商品, 共22389款好物</span>
        </router-link>
        <!--客服-->
        <div class="service" alt="cart" slot="cart">
          <span class="buy-icon-kefu kefuIcon icon iconfont "></span>
        </div>
      </buyHeader>
      <!--  头部下方分类  -->
      <div class="buyClassCon">
        <classList
          :classList="classList"
          :current="current"
          @changeHandler="changeHandler"
        ></classList>
      </div>
    </div>
    <div style="padding: 85px 0;">
      <!--  轮播图  -->
      <div class="swiper_cont">
        <banner :homeInfo="homeInfo"></banner>
      </div>
      <div v-show="homeGoodsFlag">
        <div class="buyServicePolicy">
          <span
            ><i class="buy-icon-class-14 icon iconfont"></i
            ><span>网易自营品牌</span></span
          >
          <span
            ><i class="buy-icon-tuihuotuikuan_dianpu icon iconfont "></i
            ><span>30天无忧退货</span></span
          >
          <span
            ><i class="buy-icon-daishouhuo icon iconfont "></i
            ><span>48小时快速退款</span></span
          >
        </div>
        <div class="siteClass">
          <router-link
            :to="`/category/${item.id}`"
            class="siteClassList"
            v-for="(item, index) of classInfo"
            :key="index"
          >
            <div class="siteClassItem">
              <div class="siteClassImg">
                <img
                  :src="
                    item.class_img
                      ? config.cdnbase + item.class_img + '-350'
                      : ''
                  "
                  alt=""
                />
              </div>
              <div class="siteClassTitle">{{ item.name }}</div>
            </div>
          </router-link>
        </div>
        <!--  首页商品  -->
        <div class="goodsContent">
          <homeGoods
            class="home-goods"
            v-for="(item1, index1) of homeInfo.goods"
            :key="index1"
            :goodsList="item1"
          >
          </homeGoods>
        </div>
      </div>
      <div class="classGoodsCont" v-show="!homeGoodsFlag">
        <!--分类商品-->.
        <keep-alive>
          <classGoods class="home-goods" :goodsList="classGoods"></classGoods>
        </keep-alive>
      </div>
    </div>
    <div></div>
    <tabBarFooter></tabBarFooter>
  </div>
</template>

<script>
// @ is an alias to /src
import tabBarFooter from "../components/home/tabBerFooter";
import classList from "../components/home/classList";
import homeGoods from "../components/home/homeGoods";
import classGoods from "../components/class/classGoods";
import banner from "../components/home/banner";
import { home, classify } from "../lib/goods";
import { mapState } from "vuex";

export default {
  name: "home",
  data() {
    return {
      homeInfo: {}, //首页商品
      classList: [], //分类列表
      classGoods: [], //选择分类后的商品
      current: "推荐",
      current_id: "",
      homeGoodsFlag: true
    };
  },
  components: {
    //导入组件
    classList,
    tabBarFooter,
    banner,
    homeGoods,
    classGoods
  },
  mounted() {
    this.getHomeInfo();
    this.getClassList();
    this.judgeFlage();
  },
  methods: {
    showToastTxtOnly() {
      this.toast = this.$createToast({
        txt: 'Plain txt',
        type: 'txt'
      })
      this.toast.show()
    },
    async getHomeInfo() {
      //获取首页楼层商品
      this.homeInfo = await home.get_home_goods();
    },
    getClassList() {
      //获取分类列表
      this.classInfo.map(item => {
        this.classList.push(item.name);
      });
      this.classList.unshift("推荐");
    },
    judgeFlage() {
      //页面加载时获取
      let id = this.$route.params.id;
      if (id) {
        this.homeGoodsFlag = false;
        this.getProductAll(id);
        this.classInfo.map(item => {
          if (item.id + "" === id) {
            this.current = item.name;
          }
        });
      }
    },
    changeHandler(cur) {
      this.current = cur;
      if (cur === this.classList[0]) {
        this.$router.push({
          path: "/"
        });
      } else {
        this.classInfo.map(item => {
          if (item.name === this.current) {
            this.$router.push({
              path: "/" + item.id
            });
          }
        });
      }
    },
    async getProductAll(id) {
      let classGoodsList = await classify.get_home_productAll({
        id: id
      });
      if (!classGoodsList.Error) {
        this.classGoods = classGoodsList;
      } else {
        this.classGoods = [];
      }
    }
  },
  computed: {
    ...mapState(["siteInfo", "classInfo", "language", "config"]),
    //语言转换
    lang() {
      return function(src) {
        return this.language ? this.language[src] : "";
      };
    }
  },
  watch: {
    async $route(to) {
      // 当路由改变时
      if (to.params.id !== undefined) {
        this.classGoods = []; //
        this.getProductAll(to.params.id);
        this.homeGoodsFlag = false;
      }
      if (to.params.id === undefined) {
        this.homeInfo = {};
        this.getHomeInfo();
        this.homeGoodsFlag = true;
      }
    }
  }
};
</script>

