<template>
  <div class="classGoods">
    <div class="classGoodsCont">
      <router-link
        :to="`/detail/${item1.type_id}/${item1.id}`"
        class="classGoodsList"
        v-for="(item1, index1) of goodsList"
        :key="index1"
      >
        <div class="imgCont" :style="conheight">
          <img
            v-lazy="item1.head_imgs ? config.cdnbase + item1.head_imgs : ''"
            ref="classgoodslist"
            alt=""
            :style="conheight"
          />
          <div class="classPromotion">
            {{ item1.promotion_name1 }}
            <span v-show="item1.promotion_name2">,</span>
            {{ item1.promotion_name2 }}
          </div>
        </div>
        <div class="txtCont">
          <div>
            <div class="classSitename">{{ item1.sitename }}</div>
            <div class="classPrice">{{ item1.money }}{{ item1.price }}</div>
          </div>
          <div class="statusCont">
            <p class="status gradientPrice">特价</p>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "classGoods",
  data() {
    return {
      conheight: {
        height: ""
      }
    };
  },
  props: {
    goodsList: {
      type: Array
    }
  },
  mounted() {},
  computed: {
    ...mapState(["config"])
  },
  methods: {},
  watch: {
    goodsList() {
      this.$nextTick(() => {
        if (this.$refs.classgoodslist && this.$refs.classgoodslist.length > 0) {
          this.conheight.height =
            this.$refs.classgoodslist[0].offsetWidth + "px";
        }
      });
    }
  }
};
</script>

<style scoped></style>
