<template>
  <div class="classList">
    <cube-scroll-nav-bar
      :current="current"
      :labels="classList"
      @change="changeHandler"
    />
  </div>
</template>

<script>
export default {
  name: "classList",
  //父传子用props接收数据
  props: {
    classList: {
      type: Array
    },
    current: {
      type: String
    }
  },
  methods: {
    changeHandler(cur) {
      this.$emit("changeHandler", cur);
    }
  }
};
</script>

